package com.macaosoftware.realtor.components.sections

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.css.WhiteSpace
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.navigation.UncoloredLinkVariant
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.base
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.style.vars.color.ColorVar
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.theme.colors.ColorMode
import org.jetbrains.compose.web.dom.Span
import com.macaosoftware.realtor.toSitePalette
import com.varabyte.kobweb.compose.css.CSSBackground
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.silk.components.layout.VerticalDivider
import org.jetbrains.compose.web.css.*

val FooterStyle by ComponentStyle.base {
    Modifier
        .backgroundColor(colorMode.toSitePalette().nearBackground)
        .padding(topBottom = 1.5.cssRem, leftRight = 10.percent)
}

@Composable
fun Footer(modifier: Modifier = Modifier) {
    Box(FooterStyle.toModifier().then(modifier), contentAlignment = Alignment.Center) {
        Row(
            modifier = Modifier.fillMaxWidth(),
            horizontalArrangement = Arrangement.SpaceEvenly,
            verticalAlignment = Alignment.CenterVertically
        ) {
            val sitePalette = ColorMode.current.toSitePalette()
            Link(
                "/",
                "© 2024 Inside Real Estate",
                Modifier.weight(1f).setVariable(ColorVar, sitePalette.brand.primary),
                variant = UncoloredLinkVariant
            )
            VerticalDivider(
                modifier = Modifier.width(2.px)
                    .height(20.px)
                    .background(Color.darkgray)
            )

            Row(
                modifier = Modifier.columnGap(12.px)
                    .padding(all = 4.px),
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.SpaceEvenly
            ) {
                Link(
                    "/",
                    "Terms of Use",
                    Modifier.setVariable(ColorVar, sitePalette.brand.primary),
                    variant = UncoloredLinkVariant
                )
                VerticalDivider(
                    modifier = Modifier.width(2.px)
                        .height(20.px)
                        .background(Color.darkgray)
                )
                Link(
                    "/",
                    "Privacy Policy",
                    Modifier.setVariable(ColorVar, sitePalette.brand.primary),
                    variant = UncoloredLinkVariant
                )
                VerticalDivider(
                    modifier = Modifier.width(2.px)
                        .height(20.px)
                        .background(Color.darkgray)
                )
                Link(
                    "/",
                    "Accessibility",
                    Modifier.setVariable(ColorVar, sitePalette.brand.primary),
                    variant = UncoloredLinkVariant
                )
                VerticalDivider(
                    modifier = Modifier.width(2.px)
                        .height(20.px)
                        .background(Color.darkgray)
                )
                Link(
                    "/",
                    "Fair Housing Statement",
                    Modifier.setVariable(ColorVar, sitePalette.brand.primary),
                    variant = UncoloredLinkVariant
                )
            }
        }
    }
}
