package com.macaosoftware.realtor.components.syle

import com.macaosoftware.realtor.components.util.Res
import com.varabyte.kobweb.compose.css.CSSTransition
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.background
import com.varabyte.kobweb.compose.ui.modifiers.rotate
import com.varabyte.kobweb.compose.ui.modifiers.transform
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.hover
import com.varabyte.kobweb.silk.theme.SilkTheme
import com.varabyte.kobweb.silk.theme.colors.palette.color
import org.jetbrains.compose.web.css.deg
import org.jetbrains.compose.web.css.ms

val SocialIconStyle by ComponentStyle {
    base {
        Modifier
            .rotate(0.deg)
            .background(Colors.Transparent)
            .transform {
                CSSTransition(property = "rotate", duration = 300.ms)
                CSSTransition(property = "background", duration = 300.ms)
            }
    }
    hover {
        Modifier.rotate(10.deg)
            .background(SilkTheme.palettes.light.color)
    }
}