package com.macaosoftware.realtor.pages

import androidx.compose.runtime.Composable
import com.macaosoftware.realtor.HeadlineTextStyle
import com.macaosoftware.realtor.components.layouts.PageLayout
import com.macaosoftware.realtor.components.sections.NavHeaderStyle
import com.macaosoftware.realtor.toSitePalette
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.foundation.layout.Spacer
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.color
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.style.toAttrs
