import com.varabyte.kobweb.silk.theme.colors.ColorMode
import org.jetbrains.compose.web.dom.Div
import org.jetbrains.compose.web.dom.Text

@Page("home_valuation_report")
@Composable
fun HomeValuationReport(){
   PageLayout("Home Valuation Report"){
       Row(NavHeaderStyle.toModifier()) {
           val sitePalette = ColorMode.current.toSitePalette()
           Box {
               Div(HeadlineTextStyle.toAttrs()) {
                   Text("Home Valuation Report")
                   Spacer()
                   SpanText("This is Demo Content",
                       Modifier.color(sitePalette.cobweb.toRgb()))
               }
           }
       }
   }

}